.form-input,.form-select,.textarea-dm{height:40px;min-height:40px;background:var(--color-neutral-background);font-size:16px;line-height:22px;border:1px solid var(--color-neutral-border);box-sizing:border-box;border-radius:5px;padding:0 16px;transition:box-shadow 80ms ease}.form-input_ghost,.form-select_ghost,.textarea-dm_ghost{border:none;padding:0;background-color:rgba(0,0,0,0);outline:none}.form-input_size_small,.form-select_size_small,.textarea-dm_size_small{height:24px;font-size:12px;line-height:18px;min-height:24px;padding:0 8px}input.form-input[type=radio]{width:16px;height:16px;min-height:0}input.form-input[type=range]{padding:0}.textarea-dm{padding:12px 16px;min-height:50px}