.player{width:100%;height:32px;display:flex;cursor:default;min-width:240px;border-radius:4px;align-items:center;background-color:var(--color-neutral-background);box-shadow:0 0 0 1px var(--color-neutral-border) inset,0 2px 4px rgba(var(--color-neutral-shadow-raw)/8%)}.player video{width:140px;height:50px;border-radius:4px 0 0 4px}.player_video{height:50px}.player__playback{width:100%;height:100%;display:flex;flex-direction:column}.player__controls{flex:1;height:27px}.player__play{width:24px;height:24px;display:flex;cursor:pointer;align-items:center;justify-content:center;color:var(--color-primary-icon)}.player__track,.player__time{font-size:12px;color:var(--color-neutral-content-subtler)}.player__track{white-space:nowrap;overflow:hidden;text-overflow:ellipsis;max-width:97px}.player__time{padding:4px 10px}.player__loading{width:100%;height:100%;display:flex;align-items:center;justify-content:center}