.filters-sidebar{width:var(--filters-sidebar-width);height:calc(100vh - var(--header-height) - var(--ribbon-height) - 40px);background:var(--color-neutral-background);position:relative}.filters-sidebar__container{width:100%;height:100%;overflow-y:auto}.filters-sidebar::before{content:"";position:absolute;top:0;right:100%;width:2px;height:100%;background:linear-gradient(to right, rgba(0, 0, 0, 0.1803921569), transparent);z-index:100}.filters-sidebar__header{height:36px;display:flex;position:sticky;z-index:100;background:var(--color-neutral-background);top:0;padding:0 var(--spacing-tight);align-items:center;gap:var(--spacing-tight)}.filters-sidebar__title{font-size:18px;font-weight:500}.filters-sidebar__extra{display:flex;align-items:center}.filters-sidebar__extra .button-dm{--button-width: 24px;width:var(--button-width);height:var(--button-width);padding:0;border:1px solid var(--color-neutral-border)}.filters-sidebar__extra .button-dm:hover{border:1px solid var(--color-neutral-border)}