.table-toolbar{display:flex;align-items:center;justify-content:space-between;min-width:100%;padding:0 var(--spacing-tight);background-color:var(--color-neutral-surface);height:41px;transition:transform .3s ease-in-out;transform:translateY(-41px);position:absolute}.table-toolbar_visible{position:relative;transform:translateY(0)}.table-toolbar__customize-button.table-toolbar__customize-button{height:28px;width:28px;background-color:var(--color-neutral-background);box-shadow:0 2px 5px 0 var(--black_20);border-radius:4px}.table-toolbar__customize-button.table-toolbar__customize-button:hover{border:none;filter:none;background:var(--color-primary-emphasis-subtle)}.table-toolbar__customize-button.table-toolbar__customize-button:hover__icon{color:var(--color-neutral-content)}.table-toolbar__customize-button.table-toolbar__customize-button:active{color:var(--color-neutral-content);background:linear-gradient(0deg, var(--black_2), var(--black_2)),#fff;box-shadow:0 0 5px 0 var(--black_20)}.table{width:100%;height:100%;display:flex;flex-direction:column;overflow:hidden;background-color:var(--color-neutral-background)}.table__auto-size_quick-view>div{background-color:var(--color-neutral-surface)}.table__auto-size_quick-view .table-head{width:calc(100% + var(--spacing-base));border-top:1px solid var(--color-neutral-border)}.table_fit{width:max-content}.table__sticky-header{width:100%}.table__auto-size,.table__virtual{width:auto}.table_fit .table__auto-size,.table_fit .table__virtual{width:fit-content}.table__virtual{max-width:100vw;min-width:100vw}.table__virtual>div{min-width:fit-content}.table__row-wrapper{min-width:fit-content;position:relative;cursor:pointer;background-color:var(--color-neutral-background)}.table__row-wrapper::after{content:"";position:absolute;right:0;top:0;bottom:0;flex:1;width:var(--spacing-base);height:100%;background:inherit;z-index:-1;transform:translateX(100%)}.table__row-wrapper_disabled,.table__row-wrapper_loading{opacity:.6;pointer-events:none}.table__row-wrapper_even:not(.table__row-wrapper_selected){background-color:var(--color-neutral-emphasis-subtle);position:relative}.table__row-wrapper_even:not(.table__row-wrapper_selected)::after{content:"";position:absolute;right:0;top:0;bottom:0;flex:1;width:var(--spacing-base);height:100%;background:inherit;z-index:-1;transform:translateX(100%)}.table__row-wrapper_selected{background-color:var(--color-primary-emphasis)}.table__row-wrapper:not(.table__row_wrapper_selected):hover{background-color:var(--color-primary-emphasis-subtle)}.table__cell{overflow:hidden;flex:150 0 auto;width:fit-content;min-width:min-content;max-width:min-content;padding:10px;box-sizing:border-box;display:flex;align-items:center;position:relative;word-break:break-word;color:var(--color-neutral-content-subtler)}.table__cell.show-source{padding:0}.table__cell-content{flex:0;display:flex;white-space:nowrap;align-items:center;color:var(--color-neutral-content)}.table__cell-content_disabled{opacity:.6;pointer-events:none}