.radio-group-dm{--radius: var(--corner-radius-small);--height: 40px;--padding: 3px;--font-size: 16px;--button-padding: 0 10px;--button-checked-shadow: 0 1px 0 var(--black_10), 0 0 0 1px var(--black_2), 0 5px 10px var(--black_15);height:var(--height);border-radius:var(--radius);padding:var(--padding);background:linear-gradient(0deg, var(--color-neutral-surface), var(--color-neutral-surface)),var(--color-neutral-background);border:1px solid var(--color-neutral-border)}.radio-group-dm__buttons{height:calc(var(--height) - var(--padding)*2);display:grid;grid-auto-columns:1fr;grid-auto-flow:column}.radio-group-dm__button{display:flex;opacity:.6;padding:var(--button-padding);font-weight:500;position:relative;text-align:center;align-items:center;justify-content:center;font-size:var(--font-size);border-radius:calc(var(--radius) - var(--padding));height:calc(var(--height) - var(--padding)*2 - 2px);color:var(--color-neutral-content-subtler);transition:all 100ms ease-out;cursor:pointer}.radio-group-dm__button_checked{opacity:1;background-color:var(--color-neutral-surface-hover);box-shadow:var(--button-checked-shadow);color:var(--color-neutral-content)}.radio-group-dm__button_disabled{opacity:.3;color:var(--color-neutral-subtlest);cursor:not-allowed}.radio-group-dm__button input{cursor:pointer}.radio-group-dm__input{top:0;left:0;opacity:0;width:100%;height:100%;position:absolute}.radio-group-dm_size_small{--height: 32px;--radius: var(--corner-radius-smaller);--font-size: 14px}.radio-group-dm_size_smaller{--height: 24px;--radius: 4px;--padding: 2px;--font-size: 12px;--button-padding: 0 5px;--button-checked-shadow: 0 1px 0 var(--black_10), 0 0 0 1px var(--black_2), 0 2px 4px var(--black_15)}