.annotators{display:grid;grid-auto-flow:column;grid-column-gap:4px}.annotators__item{z-index:1;width:28px;height:28px;border-radius:100%;position:relative}.annotators__item:hover{z-index:10}.userpic-badge{width:10px;height:10px;color:var(--color-neutral-background);display:flex;align-items:center;justify-content:center;border-radius:2px;box-shadow:0 0 0 2px var(--color-neutral-background)}.userpic-badge_accepted{background-color:var(--color-accent-kale-base)}.userpic-badge_rejected{background-color:var(--color-accent-persimmon-base)}.userpic-badge_fixed{background-color:var(--color-accent-canteloupe-base)}