.filterLine{position:relative;width:100%;display:grid;grid-template-columns:80px minmax(120px, max-content) minmax(100px, max-content) 1fr 24px;grid-gap:var(--spacing-tightest);align-items:start}.filterLine__remove{display:flex;align-items:center;justify-content:center;margin-top:-4px}.filterLine__remove .button-dm{height:24px;width:24px;padding:0;flex:none}.filterLine__column{min-height:24px}.filterLine__column.conjunction{display:flex;align-items:center;justify-content:flex-end}.filterLine__column.operation{display:flex;align-items:center}.filterLine__column.value{display:flex;align-items:center}.filterLine__column.field{display:flex;align-items:center}.filterLine__group{flex:0;display:grid;grid-template-columns:min-content 1fr;grid-gap:2px;align-items:flex-start}.filterLine__selector{flex:1;display:flex;align-items:center;justify-content:space-between}.filterLine .selectTrigger{padding:0 4px}.filterLine .datepicker{width:100%}