@keyframes waiting{0%{background-position:0 0}100%{background-position:37px 0}}.label-view{height:100%;width:100%;display:flex;flex-direction:column;color:var(--color-neutral-content)}.label-view__waiting{top:50%;left:50%;width:150px;height:10px;z-index:100;position:absolute;border-radius:4px;transform:translate(-50%, -50%);box-shadow:0 5px 20px var(--black_20);background-image:repeating-linear-gradient(-63.43deg, rgba(255, 255, 255, 0.2) 1px, var(--color-primary-surface) 2px, var(--color-primary-surface) 6px, rgba(255, 255, 255, 0.2) 7px, rgba(255, 255, 255, 0.2) 12px);background-color:#fff;background-size:37px 100%}.label-view__waiting_animated{animation:waiting 1s linear infinite}.label-view__header{flex:none;display:flex;min-height:56px;padding:0 1em;align-items:center;justify-content:space-between}.label-view__table{z-index:2;display:flex;height:100%;flex:200px 0 0;position:relative;flex-direction:column}.label-view__table .resizer__handle{top:0;bottom:0}.label-view__content{flex:1;display:flex;height:calc(100% - 46px)}.label-view__dataview{flex:1;width:100%;height:100%;display:flex;min-width:202px}.label-view__dataview .data-view-dm{margin-bottom:0}.label-view__lsf-wrapper{flex:1;z-index:1;min-width:0;display:flex;position:relative;box-sizing:border-box;flex-direction:column}.label-view__lsf-wrapper_mode_explorer{margin-left:1px;margin-top:-1px;border-top:1px solid var(--black_15)}.label-view_loading .label-view__lsf-container{opacity:.2;pointer-events:none}.label-view__lsf-container{width:100%;height:100%;display:flex;flex-direction:column;overflow-y:auto}.label-view__lsf-container>div{--main-bg-color: var(--color-neutral-surface);flex:1;height:100%}.label-view__lsf-toolbar{height:50px}.label-view__lsf-content{flex:1;overflow:auto;box-sizing:border-box;padding-bottom:50px}