.actionButton{--compact-height: 32px;height:var(--compact-height);padding:0 13px;display:flex;align-items:center}.actionButton__titleContainer{display:flex;align-items:center;gap:8px;justify-content:space-between}.actionButton_isTitle{font-weight:500;font-size:12px;line-height:16px;letter-spacing:.5px;color:var(--grape_500);cursor:initial;padding:16px 12px 8px;height:var(--compact-height)}.actionButton_isTitle .actionButton__titleContainer:hover{background-color:rgba(0,0,0,0)}.actionButton_isTitle .actionButton__title{font-size:12px}.actionButton_isTitle:hover{background-color:rgba(0,0,0,0) !important;color:var(--grape_500) !important}.actionButton_isSeparator{cursor:initial;height:1px;background-color:rgba(137,128,152,.12) !important;padding:0}.actionButton_isSeparator .actionButton__titleContainer{display:none}.actionButton_isSeparator.menu__item{height:1px}.actionButton_isSeparator:hover{background-color:rgba(0,0,0,0)}.actionButton_hasSeperator{border-top:1px solid rgba(137,128,152,.12)}.actionButton__title{flex-grow:1;font-size:16px}.actionButton_hasSubMenu{color:var(--color-neutral-content-subtler)}.actionButton_hasSubMenu:hover{color:var(--black);background-color:var(--black_4)}.actionButton_danger{color:#d00}.actionButton-submenu{margin:0;padding:0;list-style:none}