.range{display:inline-flex;align-items:center;user-select:none}.range_align_horizontal{min-width:120px;height:28px;flex-direction:row}.range_align_vertical{min-height:120px;width:28px;flex-direction:column}.range_with-icon .range_align_horizontal{padding:0 5px}.range_with-icon .range_align_vertical{padding:5px 0}.range__body{position:relative}.range_align_horizontal .range__body{height:4px;width:100%}.range_align_vertical .range__body{height:100%;width:4px}.range_align_horizontal .range__body_with-icon{margin:0 5px}.range_align_vertical .range__body_with-icon{margin:5px 0}.range__line{background:linear-gradient(0deg, var(--color-neutral-surface-active), var(--color-neutral-surface-active)),#fff;box-shadow:inset 0 1px 0 rgba(var(--color-neutral-shadow-raw)/5%),inset 0 0 0 1px rgba(var(--color-neutral-shadow-raw)/5%);border-radius:8px}.range_align_horizontal .range__line{height:4px;width:100%}.range_align_vertical .range__line{width:4px;height:100%}.range__indicator{border-radius:8px;position:absolute;background-color:var(--color-primary-emphasis)}.range_align_horizontal .range__indicator{top:0;height:4px}.range_align_vertical .range__indicator{left:0;width:4px}.range__range-handle{width:0;height:0;position:absolute}.range__range-handle::before{content:"";position:absolute;background:var(--color-primary-surface);box-shadow:0 5px 10px rgba(var(--color-primary-shadow-raw)/30%);border-radius:8px;transform:translate3d(-50%, -50%, 0)}.range__range-handle::after{content:"";width:25px;height:25px;position:absolute;transform:translate3d(-50%, -50%, 0)}.range_align_horizontal .range__range-handle{top:50%;cursor:col-resize}.range_align_horizontal .range__range-handle::before{width:16px;height:16px}.range_align_vertical .range__range-handle{left:50%;cursor:row-resize}.range_align_vertical .range__range-handle::before{width:16px;height:16px}.range__icon{width:28px;height:28px;display:flex;align-items:center;justify-content:center}